<template>
  <div class="home">
    <div v-if="authHeader === null">
      <input type="text" v-model="key" placeholder="access code"/>
      <button @click="submit">Enter Code</button>
    </div>
    <div v-else>
      <p>You have entered a code, if you ended up here you need to re-enter the code. Hit refresh to start over.</p>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
  name: 'Home',
  data () {
    return {
      key: null
    }
  },
  methods: {
    ...mapActions([
      'setAuthHeader'
    ]),
    submit () {
      this.setAuthHeader(this.key)
    }
  },
  computed: {
    ...mapGetters([
      'authHeader'
    ])
  }
}
</script>
